.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}

.loader {
  margin: 60px auto;
  font-size: 5px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(89, 194, 112, 0.2);
  border-right: 1.1em solid rgba(89, 194, 112, 0.2);
  border-bottom: 1.1em solid rgba(89, 194, 112, 0.2);
  border-left: 1.1em solid #56b280;
  transform: translateZ(0);
  animation: load 1.7s infinite linear;
}

.loader-sm {
  width: 24px;
  height: 24px;
  border-top: 4px solid rgba(77, 77, 77, 0.2);
  border-right: 4px solid rgba(77, 77, 77, 0.2);
  border-bottom: 4px solid rgba(77, 77, 77, 0.2);
  border-left: 4px solid #cacaca;
  animation: load 1.1s infinite linear;
  display: inline-block;
  margin: 0;
  padding: 0;
  border-radius: 50%;
}

@keyframes load {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

